*{
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  line-height: 1.6em;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* random links inside paragraphs must have this class */
.asper{
  text-underline-offset:0.25em;
}
.asper:link,
.asper:visited{
  color: inherit;
}
.asper:hover{
  color: #666666;
}

.big-bottom{
  margin-bottom: 32px;
}
.small-bottom{
  margin-bottom: 24px;
}


/* nav bar */

.nav{
  background-color: transparent;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 5em;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content:right;
  padding: 1.2em 0.8em 0 0;
}

.nav--links{
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: flex-end;
}

.nav--link{
  color: #6e6e6e;
  font-size: 1.2em;
  padding: 0.2em 1em;
  margin: 2px;
  border: 2px solid transparent;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  transition: .3s;
}

.nav--link:hover{
  cursor: pointer;
  color: #EAA01F;
  border-bottom: 2px solid #EAA01F;
}

.nav--link:focus{
  color: #EAA01F;
}

.container{
  scroll-snap-type: y proximity; /* mandatory */
  overflow-y: scroll;
  height: 100vh;
}

.bod{
  min-height: 100vh;
  width: 100vw;
  padding: 120px 25px 25px;
  padding-left: 50px;
  display: flex;
  flex-direction: column;
  align-items: justify;
  justify-content:baseline;
  scroll-snap-align: start;
  font-size: 1.4em;
}


.bod1{
  background-color: #fafafa;
  color: #212121;
}

.bod2{
  background-color: #212121;
  color: #fafafa;
}

.contact--footer,
#contact{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

/* about */
#about--container{
  max-width: 45em;
}

#about--container>ul{
  list-style-type: none;
}

/* icons */

.contact--icons>ul{
  display: flex;
  list-style-type: none;
  align-items:baseline;
  justify-content:center;
}

/* coloring and animatingicons on hover */

.icons{
  height: 36px;
  filter: invert(36%);
  margin: 10px;
  transition: 0.3s;
}

.icons:hover{/* 
  margin-top: 2px; */
  padding: 2px;
  filter: invert(100%);
}

.to-top{
  height: 64px;
  position: fixed;
  bottom: 2em;
  right: 2em;
  transition: .3s;
}

.to-top:hover{
  cursor: pointer;
  filter: brightness(50%) invert(100%);
  padding: 4px;
}

/* contact logo */
.contact--logo{
  height: 300px;
  margin: 50px ;
}

/* email */
.contact--email{
  margin: 20px;
}
.email{
  color: #EAA01F;
  font-size: 0.7em;
  padding: 4px;
  border: 1px solid transparent;
  text-decoration: none;
  transition: 0.3s;
}

.email:hover{
  cursor: pointer;
  border-bottom: 1px solid #EAA01F;
}

/* copyright */
.copyright{
  color: #666666;
  font-size: 0.5em;
  text-transform: uppercase;
}


/* buttons */
.button{
  color: #EAA01F;
  font-size: 1.1em;
  font-weight: 600;
  min-width: 100px;
  background: none;
  border: 1px solid #EAA01F;
  padding: 0.4em 0.8em;
  margin: 0 20px 0;
  border-radius: 4px;
  transition: 0.3s;
}

.button:hover{
  cursor: pointer;
  background-color: #EAA01F;
  color: #212121;
}